import React from 'react';
import { Card, CardContent, Typography, Divider, Avatar, Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Avator from '../img/img9.jpg';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
    margin: 'auto',
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    padding: theme.spacing(2),
  },
  date: {
    fontSize: '0.75rem',
    color: theme.palette.text.secondary,
  },
  title: {
    marginTop: theme.spacing(1),
    fontSize: '1.25rem',
    fontWeight: 'bold',
  },
  subtitle: {
    marginTop: theme.spacing(1),
    fontSize: '1rem',
    color: theme.palette.text.secondary,
  },
  divider: {
    margin: theme.spacing(2, 0),
  },
  profileContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  avatar: {
    width: theme.spacing(5),
    height: theme.spacing(5),
    marginRight: theme.spacing(1),
  },
  author: {
    fontSize: '1rem',
  },
}));

const BlogCard = () => {
  const classes = useStyles();
  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography className={classes.date}>October 23, 2019</Typography>
        <Typography className={classes.title}>Warm spring is coming!</Typography>
        <Typography className={classes.subtitle}>
          Australian trees sweat to survive extreme heatwaves
        </Typography>
        <Divider className={classes.divider} />
        <Grid container alignItems="center" className={classes.profileContainer}>
          <Avatar
            alt="Tony Kaker"
            src={Avator}
            className={classes.avatar}
          />
          <Typography className={classes.author}>Tony Kaker</Typography>
        </Grid>
      </CardContent>
    </Card>
  );
};

const BlogList = () => {
  return (
    <Grid container spacing={3}>
      {[...Array(5).keys()].map((index) => (
        <Grid item key={index} xs={12} sm={6} md={4}>
          <BlogCard />
        </Grid>
      ))}
    </Grid>
  );
};

export default BlogList;
