import React, { useState } from 'react';
import { AppBar, Toolbar, IconButton, Typography, Container, Grid, Button, Drawer, List, ListItem, ListItemText, useMediaQuery, useTheme } from '@material-ui/core';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCaretDown } from '@fortawesome/free-solid-svg-icons';
import MenuIcon from '@material-ui/icons/Menu';
import BlogList from './Blogcard'; // Import your BlogList component
import Shop from './ProductCards'; // Import your Shop component
import Pagescards from './PagesCards';
import DocsCard from './DocsCard';
import DemosCards from './DemosCards';


const Home = () => <div></div>;
const Pages = () => <div></div>;
const Blogs = () => <div></div>;
const Demos = () => <div></div>;
const Docs = () => <div></div>;

const SecondPanel = () => {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  const handleMobileMenuToggle = () => {
    setMobileMenuOpen(!mobileMenuOpen);
  };

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const buttonStyle = {
    color: 'gray',
    '&:hover': {
      color: 'black',
    },
  };

  const panelStyle = {
    backgroundColor: 'white',
    color: 'black',
    boxShadow: 'none',
  };

  const drawerItems = (
    <List>
      {['Home', 'Pages', 'Blogs', 'Shop', 'Demos', 'Docs'].map((text) => (
        <ListItem button key={text} component={Link} to={`/${text.toLowerCase()}`} onClick={handleMobileMenuToggle}>
          <ListItemText primary={text} />
        </ListItem>
      ))}
    </List>
  );

  return (
    <Router>
      <AppBar position="static" style={panelStyle}>
        <Container>
          <Toolbar style={{ justifyContent: 'space-between' }}>
            <Typography variant="h6" style={{ fontWeight: 'bold', marginRight: 'auto', fontSize: isMobile ? '24px' : '40px', color: '#377DFF' }}>
              Front
            </Typography>

            {isMobile ? (
              <IconButton
                edge="start"
                color="inherit"
                aria-label="menu"
                onClick={handleMobileMenuToggle}
              >
                <MenuIcon />
              </IconButton>
            ) : (
              <Grid container spacing={2} alignItems="center" justify="flex-end">
                {['Home', 'Pages', 'Blogs', 'Shop', 'Demos', 'Docs'].map((item) => (
                  <Grid item key={item}>
                    <Button
                      style={{ ...buttonStyle, fontSize: '17px' }}
                      color="inherit"
                      component={Link}
                      to={`/${item.toLowerCase()}`}
                    >
                      {item} <FontAwesomeIcon icon={faCaretDown} />
                    </Button>
                  </Grid>
                ))}
              </Grid>
            )}

            <Drawer open={mobileMenuOpen} onClose={handleMobileMenuToggle}>
              <div
                style={{ width: '200px' }}
                role="presentation"
                onClick={handleMobileMenuToggle}
                onKeyDown={handleMobileMenuToggle}
              >
                {drawerItems}
              </div>
            </Drawer>

            {!isMobile && (
              <Button color="inherit" variant="contained" style={{ backgroundColor: '#377DFF', color: 'white', padding: '10px', fontWeight: 'bold', minWidth: '120px' }}>
                Buy Now
              </Button>
            )}
          </Toolbar>
        </Container>
      </AppBar>

      <Container>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/home" element={<Home />} />
          <Route path="/pages" element={<Pagescards />} />
          <Route path="/blogs" element={<BlogList />} />
          <Route path="/shop" element={<Shop />} />
          <Route path="/demos" element={<DemosCards />} />
          <Route path="/docs" element={<DocsCard />} />
        </Routes>
      </Container>
    </Router>
  );
};

export default SecondPanel;

